
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import DeviceMaintainItem from './DeviceMaintainItem'

export default {
name: 'DeviceMaintainList',
components: {
DeviceMaintainItem,
Group,
Cell,
CellBox
},
props: {
device_id: ''
},
methods: {
formatMaintain (maintain) {
return {
id: maintain.id,
name: maintain.name
}
}
},
mounted () {
this.axios
.get('http://api.lams.com/maintains')
.then(response => {
if (response.data.code === 200) {
this.maintains = response.data.data
} else {
console.log(response.data.message)
}
})
.catch(error => {
console.log(error)
var arr = []
for (var i = 0; i < 1; i++) {
arr.push({
'id': '1',
'name': 'ivc',
'description': 'ivcivcivcivcivcivc',
'starts': 4
})
arr.push({
'id': '2',
'name': 'bbb',
'description': 'ivcivcivcivcivcivc',
'starts': 5
})
}
this.maintains = arr
})
.finally()
},
data () {
return {
maintains: [{'id': '4',
'name': 'ivc',
'description': 'ivcivcivcivcivcivc',
'starts': 4}]
}
}
}



